@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css?family=Hind&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}

p,h1,ul,li {
  margin: 0;
  padding: 0;
}

button{
  font-size: 24px;
  padding: .5rem 1rem;
  margin: .5rem;
  box-sizing: border-box;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  border: none;
  outline: none;
}

button:disabled,
button[disabled],
#add[disabled] {
  background-color: #ccc;
  cursor: not-allowed;
}

button:hover {
  box-shadow: inset 0 0 0 10em rgba(255, 255, 255, 0.3);
}
.App {
  max-width: 50rem;
  margin: 0 auto;
}


.default-text {
  text-align: center;
}

.error-text {
  text-align: center;
  color: red;
}

#add{
  background-color: #4c8bf5;
}

#add:active {
  background: #2f5aa2;
  box-shadow: inset 0px 0px 5px #244172;
   outline: none;
}

#remove{
  background-color: white;
  box-sizing: border-box;
  border: 3px solid #f55853;
  color: #f55853;
}

#remove:active {
  background: #e9e9e9;
  box-shadow: inset 0px 0px 5px #e9e9e9;
   outline: none;
}

#remove:hover{ 
  border: 3px solid #ff0700;
  color: #ff0700;
}


.movies-container {
  display: flex;

}

.movies {
  flex: 1 1;
  margin: 0 .5rem;
}

.movies-header-text {
  font-family: "Hind", sans-serif;
  text-align: center;
}

.banner {
  width: 100%;
  margin: 1rem 0px;
}

.app-title {
  position: relative;
  stroke-width: 110px * 0.2;
  font-family: 'Pacifico', cursive;
  text-transform: uppercase;
  font-size: 3rem;
  color: #e2e9f4;
  text-align: center;
}

.app-title:after {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-text-stroke-width: 0;
  color: red;
}

.app-title:before {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
}
.Card {
  padding: 1px;
  border-radius: .5rem;
  background: linear-gradient(-67deg, rgba(#c8d8e7, .7), rgba(255,255,255,.8));
  overflow: hidden;
  box-shadow: 
    -2px -2px 6px rgba(#fff, .6),
    2px 2px 12px #c8d8e7;
  width: 100%;
}

.CardInner {
  padding: 16px 16px;
  background-color: #e2e9f4;
  border-radius: .5rem;
  width: 100%;
}

.container {
  display: flex;
}

.Icon {
  min-width: 46px;
  min-height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--border-radius);
  margin-right: 12px;
  box-shadow: 
    -2px -2px 6px rgba(#fff, .6),
    2px 2px 12px #c8d8e7;
}

svg {
  transform: translate(-1px, -1px);    
}

label {
  font-family: "Hind", sans-serif;
  display: block;
  color: #3c4b66;
  margin-bottom: 12px;
  background: linear-gradient(45deg, rgba(#6b7b8f, 1), #3c4b66);
}

.InputContainer {
  width: 100%;
}

input {
  background-color: #e3edf7;
  padding: 16px 32px;
  border: none;
  display: block;
  font-family: 'Orbitron', sans-serif;
  font-weight: 600;
  color: #a9b8c9;
  -webkit-appearance: none;
  transition: all 240ms ease-out;
  width: 100%;
};

input::-webkit-input-placeholder {
  color: #6d7f8f;
}

input:-ms-input-placeholder {
  color: #6d7f8f;
}

input::-ms-input-placeholder {
  color: #6d7f8f;
}

input::placeholder {
  color: #6d7f8f;
}

input:focus {
  outline: none;
  color: #6d7f8f;
  background-color: lighten(#e3edf7, 3%);
}
  
.InputContainer {
  --top-shadow: inset 1px 1px 3px #c5d4e3, inset 2px 2px 6px #c5d4e3;
  --bottom-shadow: inset -2px -2px 4px rgba(255,255,255, .7);
  
  position: relative;
  border-radius: var(--border-radius);
  overflow: hidden;
}
.movie-container {
  margin: 0 0 .5rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  box-shadow: 3px 7px 23px 1px rgba(0,0,0,0.66);
  background-color: white;
  color: black;
}

.movie-title {
  font-family: 'Orbitron', sans-serif;
}
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid red;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
